import React, {useState} from 'react';
import { useDispatch, useSelector } from "react-redux";
import {useFormik} from 'formik';
import * as yup from 'yup';
import { addUserAction } from '../../redux/actions/UserAction';
import { maNhom } from '../../configs/settings';

export default function EditUser(props) {

    // const [dsUser, setDSUser] = useState(null);
    const dispatch = useDispatch();
    const {accessToken} = useSelector(state => state.UserReducer);

     
    const {handleChange, handleSubmit, errors, handleBlur, touched, isValid} = useFormik({
        initialValues: {
            maNhom: "GP06",
            taiKhoan: "",
            matKhau: "",
            email: "",
            hoTen: "",
            soDt: "",
            maLoaiNguoiDung: "",
        },
        validationSchema: yup.object().shape({
           maNhom: yup.string().required("Ti khon khng c b trng!"),
           taiKhoan: yup.string().required("Ti khon khng c b trng!"),
           matKhau: yup.string().required("Mt khu khng c b trng!").min(6, "Ti khon ti thiu phi 6 k t"),
           email: yup.string().required("Email khng c b trng!").matches(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/, "Email khng hp l"),
           hoTen: yup.string().required("H tn khng c b trng!").matches( /^[a-zA-Z_" +"" +"\\s]+$/, "H tn phi nhp ch"),
           soDt: yup.string().required("S in thoi khng c b trng!").matches(/^[0-9]+$/, "S in thoi bt buc phi l s "),
           maLoaiNguoiDung: yup.string().required("M loi ngi dng khng c b trng!"),
        }),
        onSubmit: (value) => {
           dispatch(addUserAction(value,accessToken))
           console.log(value);
        }
    })

    


    return (
        <div className="block">
            <div className="modal fade" id="edituser" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="exampleModalLabel">Sa ngi dng</h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true"></span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <form className="form-add-user"onSubmit={handleSubmit}>
                                <div className="row form-group">
                                    <label for="" className="col-md-2">M nhm</label>
                                    <div className="col-md-10">
                                        <input type="text" name="maNhom" className="form-control" value={maNhom} disabled onChange={handleChange} onBlur={handleBlur} />
                                        {errors.maNhom && touched.maNhom ? (<p className="text-danger">{errors.maNhom}</p>) : ("")}
                                    </div>
                                </div>
                                <div className="row form-group">
                                    <label for="" className="col-md-2">Ti Khon</label>
                                    <div className="col-md-10">
                                        <input type="text" name="taiKhoan" className="form-control" placeholder="Nhp ti khon" onChange={handleChange} onBlur={handleBlur} />
                                        {errors.taiKhoan && touched.taiKhoan ? (<p className="text-danger">{errors.taiKhoan}</p>) : ("")}
                                    </div>
                                </div>
                                <div className="row form-group">
                                    <label for="" className="col-md-2">Mt Khu</label>
                                    <div className="col-md-10">
                                        <input type="password" name="matKhau" className="form-control" placeholder="Nhp mt khu" onChange={handleChange} onBlur={handleBlur} />
                                        {errors.matKhau && touched.matKhau ? (<p className="text-danger">{errors.matKhau}</p>) : ("")}
                                    </div>
                                </div>
                                <div className="row form-group">
                                    <label for="" className="col-md-2">Email</label>
                                    <div className="col-md-10">
                                        <input type="text" name="email" className="form-control" placeholder="Nhp email" onChange={handleChange} onBlur={handleBlur} />
                                        {errors.email && touched.email ? (<p className="text-danger">{errors.email}</p>) : ("")}
                                    </div>
                                </div>
                                <div className="row form-group">
                                    <label for="" className="col-md-2">H tn</label>
                                    <div className="col-md-10">
                                        <input type="text" name="hoTen" className="form-control" placeholder="Nhp h tn" onChange={handleChange} onBlur={handleBlur} />
                                        {errors.hoTen && touched.hoTen ? (<p className="text-danger">{errors.hoTen}</p>) : ("")}
                                    </div>
                                </div>
                                <div className="row form-group">
                                    <label for="" className="col-md-2">S in thoi</label>
                                    <div className="col-md-10">
                                        <input type="text" name="soDt" className="form-control" placeholder="Nhp s in thoi" onChange={handleChange} onBlur={handleBlur} />
                                        {errors.soDt && touched.soDt ? (<p className="text-danger">{errors.soDt}</p>) : ("")}
                                    </div>
                                </div>
                                <div className="row form-group">
                                    <label for="" className="col-md-2">M loi ngi dng</label>
                                    <div className="col-md-10">
                                        <select name="maLoaiNguoiDung" className="form-control" onChange={handleChange} onBlur={handleBlur}>
                                            <option value="ChonLoaiNguoiDung">Chn loi ngi dng</option>
                                            <option value="QuanTri">Qun Tr</option>
                                            <option value="KhachHang">Khch Hng</option>
                                        </select>
                                        {errors.maLoaiNguoiDung && errors.maLoaiNguoiDung ? (<p className="text-danger">{errors.maLoaiNguoiDung}</p>) : ("")}
                                    </div>
                                </div>
                                <button className="btn btn-submit" type="submit">Thm Ngi Dng</button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
