import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import { useForm, Controller } from "react-hook-form";

import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { ListLogoAction, ListCenemaShowAction, ListMovieShowTimeAction} from '../../redux/actions/ShowTimeMovieAction';

export default function ModalShowCalendar() {

    const {accessToken} = useSelector(state => state.UserReducer);
    const {listLogoMovie, listMovie, listCinema} = useSelector(state => state.ShowTimeMovieReducer);

    const dispatch = useDispatch();

    const { register, handleSubmit, formState: {errors}, control,} = useForm();
    
    //   const onSubmit = (data) => {
    //     dispatch((data, accessToken));
    //   };
    
    useEffect(() => {
        dispatch(ListLogoAction())
        // dispatch(detailsMoviesAction())
    },[])
    // useEffect(() => {
    //     dispatch(ListCenemaShowAction())
    // },[])

    // Ly tn h thng rp
    const renderHeThongRap = () => {
        return listLogoMovie?.map((item,index)=>{
            return (
                <option key={index} value={item.maHeThongRap}>{item.tenHeThongRap}</option>
            )
        })
    }
    //Ly cm rp
    const renderCumRap = () => {
            return listCinema?.map((item,index)=>{
                return (
                    <option key={index} value={item.maCumRap}>{item.tenCumRap}</option>
                )
            })
      
    }
    //rap
    // const renderRap = () => {
    //     return listCinema?.map((item1,index)=>{
    //         return item1.danhSachRap?.map((item,index)=>{
    //             return (
    //                 <option key={index} value={item.maRap}>{item.tenRap}</option>
    //             )
    //         })
    //     })
    // }
      
    return (
        <div className="modal fade modal-calendar" id="showcalendar" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog modal-dialog-centered">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title" id="exampleModalLabel">Lch chiu</h5>
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true"></span>
                        </button>
                    </div>
                    <div className="modal-body">
                        <form className="form">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="row form-group">
                                        <div className="item-label col-md-3">
                                            <label for="">H thng rp</label>
                                        </div>
                                        <div className="select-setion col-md-9">
                                            <select className="form-control" placeholder="Chn h thng rp">
                                                {renderHeThongRap()}
                                            </select>
                                        </div>
                                    </div>
                                    <div className="row form-group">
                                        <div className="item-label col-md-3">
                                            <label for="">Cm rp</label>
                                        </div>
                                        <div className="select-setion col-md-9">
                                            <select className="form-control" placeholder="Chn cm rp">
                                                {renderCumRap()}
                                            </select>
                                        </div>
                                    </div>
                                    <div className="row form-group">
                                        <div className="item-label col-md-3">
                                            <label for="">Rp</label>
                                        </div>
                                        <div className="select-setion col-md-9">
                                            <select className="form-control" placeholder="Chn rp">
                                                {/* {renderRap()} */}
                                            </select>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="row form-group">
                                        <div className="item-label col-md-3">
                                            <label for="ngayChieuGioChieu">Ngy Chiu</label>
                                        </div>
                                        <div className="select-setion col-md-9">
                                            <Controller name="ngayChieuGioChieu"  control={control} defaultValue={null}  render={({ field }) => (
                                                <DatePicker onChange={(e) => field.onChange(e)} selected={field.value} placeholderText = "Vui lng chn" dateFormat="dd/MM/yyyy" isClearable />
                                            )} className="form-control" type="text" placeholder="Chn ngy chiu" />
                                        </div>
                                    </div>
                                    <div className="row form-group">
                                        <div className="item-label col-md-3">
                                            <label for="">Thi lng</label>
                                        </div>
                                        <div className="select-setion col-md-9">
                                            <select className="form-control" placeholder="Chn thi lng">
                                                <option value={120}>120 pht</option>
                                            </select>
                                        </div>
                                    </div>
                                    <div className="row form-group">
                                        <div className="item-label col-md-3">
                                            <label for="">M nhm</label>
                                        </div>
                                        <div className="select-setion col-md-9">
                                            <input className="form-control" type="text" disabled name="" />
                                        </div>
                                    </div>
                                    <div className="row form-group">
                                        <div className="item-label col-md-3">
                                            <label for="">Gi v</label>
                                        </div>
                                        <div className="select-setion col-md-9">
                                            <select className="form-control" placeholder="Chn gi v">
                                                <option value={75000}>75000</option>
                                                <option value={90000}>90000</option>
                                                <option value={120000}>120000</option>
                                            </select>
                                        </div>
                                    </div>
                                </div>
                                <div className="btn-item">
                                    <button type="submit" className="btn btn-add">To lch chiu</button>
                                    <a href="#" className="btn btn-back">Tr v trang trc</a>
                                </div>
                            </div>
                            {/* show infor calendar */}
                            <div className="select-infor">
                                <h2 className="ttl-2">Chn thng tin hin th lch chiu ca phim</h2>
                                <div className="row">
                                    <div className="col-md-6">
                                        <div className="row form-group">
                                            <div className="item-label col-md-3">
                                                <label for="">H thng rp</label>
                                            </div>
                                            <div className="select-setion col-md-9">
                                                <select className="form-control">
                                                    <option>Chn h thng rp</option>
                                                </select>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="row form-group">
                                            <div className="item-label col-md-3">
                                                <label for="">Cm rp</label>
                                            </div>
                                            <div className="select-setion col-md-9">
                                                <select className="form-control">
                                                    <option>Chn cm rp</option>
                                                </select>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <table className="table table-show-infor">
                                    <thead>
                                        <tr>
                                            <th>M lch chiu</th>
                                            <th>Ngy chiu gi chiu</th>
                                            <th>Gi v</th>
                                            <th>Thi lng</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr>
                                            <td></td>
                                            <td></td>
                                            <td></td>
                                            <td></td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                            {/* end show infor calendar */}
                        </form>
                    </div>
                </div>
            </div>
    </div>
    )
}
